import React from "react";
import "./History.css";
import { BadgeCheck as Certificate } from "lucide-react"; 

function History() 
{
  const timelineEvents = [
    {
      year: 2019,
      title: " ",
      description: ["B5G        "],
    },
    {
      year: 2020,
      title: "?",
      description: ["       ",
        "     /   ",
        "AI    ",
        "     (GUI)  "],
    },
    {
      year: 2021,
      title: "?",
      description: ["5G NR    ", "   (GUI) "],
    },
    {
      year: 2022,
      title: "?",
      description: ["5G NSA     ", "X, Ku      ",
          "     "],
    },
    {
      year: 2023,
      title: "?",
      description: ["        ",
          "gSM       ", "    H/W      ",
          "  5G   ", "   URTDN   "],
    },
    {
      year: 2024,
      title: "?",
      description: ["    HW ", "5G SA   ",
          "16       ", 
          "            "
      ],
    },
  ];

  const certifications = [
    " (2019.3)",
  ];

  return (
    <div className="our-journey-container">
      
      <section className="timeline-section">
        <h1 className="section-title">Our History</h1>
        <div className="timeline">
          {timelineEvents.map((event, index) => (
            <div key={index} className="timeline-item">
              <div className="timeline-year">{event.year}</div>
              <div className="timeline-details">
                <h2 className="timeline-title">{event.title}</h2>
                <ul className="timeline-description-list">
                  {event.description.map((desc, i) => (
                    <li key={i} className="timeline-description-item">
                      {desc}
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          ))}
        </div>
      </section>

      <section className="certification-section">
        <h2 className="section-title"></h2>
        <div className="cards-container">
          <div className="card">
            <div className="card-header">
              <Certificate className="card-icon" sx={{ fontSize: 25 }}/>
              <h3 className="card-title"> </h3>
            </div>
            <ul className="card-content">
              {certifications.map((certification, index) => (
                <li key={index}>{certification}</li>
              ))}
            </ul>
          </div>

          <div className="certification-img">
            <img
              className="certi-img"
              src="./imgfile/.jpg"
              alt=""
              width={200}
              height={300}
            >
            </img>
          </div>
        </div>
      </section>
    </div>
  );
}

export default History;



